// COMMON
.icon {@extend %icon;}

.about__title,
.about__label,
.about__text { @extend %del-margin;}

.about__img { @extend %img-settings;}


// SECTION ABOUT
.section {
    background-color: $green-bg-cl;
        padding-top: 60px;
        padding-bottom: 50px;

    @media screen and (min-width:$tablet) {
        padding-top: 60px;
        padding-bottom: 86px; /*need to check*/
    
        }
    
    @media screen and (min-width:$desktop) {
        padding-top: 60px;
        padding-bottom: 138px;/*need to check*/
    }
}

.about__label {
   
    display: block;
    font-family: $main-font;
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: calc(22/18);
  
    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;

    color: $white-txt-cl;

    @media screen and (max-width: calc(#{$tablet} - 1px)) {
            max-width: 210px;
            margin-left: auto;
            margin-right: auto;
        }

    @media screen and (min-width: $tablet) {
    font-weight: 800;
    font-size: 22px;
    line-height: calc(27/22)}

    @media screen and (min-width: $desktop) {
    font-weight: 800;
    font-size: 30px;
    line-height: calc(37/30)
    }

}
.about__title {
    margin-top: 12px;
    font-family: $secondary-font;
    font-style: normal;
    font-weight: 400;
    font-size: 30px;
    line-height: calc(38/30);
    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;

    color: $white-txt-cl;
    
    @media screen and (min-width: $tablet) {
    font-size: 34px;
    line-height: calc(43/34);
    letter-spacing: 0.06em;}

    @media screen and (min-width: $desktop) {
    font-size: 36px;
    line-height: calc(45/36);
    letter-spacing: 0.06em;
    }

}

// ANIMATION
.about__title {
    animation: slide-in-elliptic-top-fwd 1000ms cubic-bezier(0.4, 0, 0.2, 1) 1000ms both;
}

@keyframes slide-in-elliptic-top-fwd {
    0% {

        transform: translateY(-600px) rotateX(-30deg) scale(0);
        transform-origin: 50% 100%;
        opacity: 0;
    }

    100% {
        transform: translateY(0) rotateX(0) scale(1);
        transform-origin: 50% 1400px;
        opacity: 1;
    }
}

.about__thumb {
    position: relative;
    background-color: $yellow-bg-cl;
    border-radius: 50%;
    z-index: 10;
    width: 280px;
    height: 280px;

 @media screen and (max-width: calc(#{$tablet} - 1px)) {
        
        margin-top: 102px;
        margin-bottom: 66px;
        margin-left: auto;
        margin-right: auto;
    
 }

 @media screen and (min-width:$tablet) {
        width: 330px;
        height: 330px;

 }

 @media screen and (min-width:$desktop) {
        width: 490px;
        height: 490px;

 }}

.about__photo {
    position: absolute;
    left: 0;
    top: 22px;
    width: 280px;
    height: 280px;
    border-radius: 50%;
    overflow: hidden;
    object-fit: contain;
    object-position: center;
    z-index: 20;

  
    @media screen and (min-width:$tablet) {       
        top: 26px;
        width: 330px;
        height: 330px;
        
    }
    
    @media screen and (min-width:$desktop) {
        top: 39px;
        width: 490px;
        height: 490px;
        
    }
}

.about__img {
    display: block;
    width: 100%;
    height: 100%;

// ANIMATION
    @include transitionRules (transform);

    .about__photo:hover & {
        transform: scale(1.1);
            }
}


.about__list {
    @media screen and (max-width: calc(#{$tablet} - 1px)) {
        padding-left: 50px;
        padding-right: 50px;
        margin-bottom: 10px;
    
    }
    @media screen and (min-width:$tablet) {
        margin-bottom: 32px;

    }

    @media screen and (min-width:$desktop) {
        margin-bottom: 54px;

    }
    
}
.about__item:last-child {
    @media screen and (max-width: calc(#{$tablet} - 1px)) {
        display: none;

    }

    @media screen and (min-width:$tablet)  {
        .about__text {  line-height: calc(23/12);

    }

}}

.about__item:first-child {
    @media screen and (max-width: calc(#{$tablet} - 1px)) {
        margin-bottom: 4px;
        .about__text {
            line-height: calc(21/12);}
    
        }
    @media screen and (min-width:$desktop) {
        .about__text {
            font-size: 16px;
            line-height: calc(28/16);
            }
    }
}

.about__item:not(:last-child ){
    @media screen and (min-width:$tablet){
    margin-bottom: 8px;}
}


.about__text {
    font-family: $main-font;
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: calc(19/12);
    text-align: center;
    letter-spacing: 0.04em;

    color: $white-txt-cl;
    @media screen and (min-width:$tablet) {
        line-height: calc(21/12);
        text-align: left;
    }
    @media screen and (min-width:$desktop) {
    font-size: 14px;
    line-height: calc(27/14);}
    
    
}


.about__wrapper {

    @media screen and (min-width:$tablet) {
        display: flex;
        justify-content: space-between;
        margin-top: 72px;

        .about__text-button {
            width: 330px; /*need to think and check text-size*/
        }
    }

    @media screen and (min-width:$desktop) {
        display: flex;
        justify-content: space-between;
        margin-top: 82px;

        .about__text-button {
                width: 490px; /*need to think and check text-size*/
            }

    }
}

.about__text-button {
    @media screen and (min-width:$tablet) {
        padding-top: 8px; 
        
    }
    @media screen and (min-width:$desktop) {
        padding-top: 90px; 
        
    }
}